<template>
  <div>
    <el-dialog
      title="决定面试结果"
      :visible.sync="decideVisible"
      width="30%"
      :close-on-click-modal="false"
    >
      <el-radio v-model="decideResult" label="录用">录用</el-radio>
      <el-radio v-model="decideResult" label="拒绝">拒绝</el-radio>
      <el-radio
        v-model="decideResult"
        label="进入下一轮"
        :disabled="this.noUndecidedData || this.undecidedData[this.currentIndex].index === 2"
      >进入下一轮</el-radio>
      <span slot="footer">
        <el-button
          type="primary"
          @click="handleConfirm"
          v-loading.fullscreen.lock="loading"
          element-loading-text="发送邮件中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >确定</el-button>
        <el-button @click="decideVisible = false">返回</el-button>
      </span>
    </el-dialog>

    <NavMenu activeIndex="3" />
    <el-dialog
      title="面试官评价"
      :visible.sync="dialogVisible"
      width="40%"
      :close-on-click-modal="false"
    >
      <p class="dialog">{{ dialogText }}</p>
      <span slot="footer">
        <el-button type="primary" @click="dialogVisible = false">返回</el-button>
      </span>
    </el-dialog>
    <el-tabs type="border-card" class="tabs" @tab-click="handleClick">
      <el-tab-pane label="待处理面试结果">
        <div class="tag" v-if="noUndecidedData">
          <i class="el-icon-warning-outline"></i>
          目前暂无待处理面试结果
        </div>
        <el-table :data="undecidedData" :height="684" v-if="!noUndecidedData">
          <el-table-column prop="interviewee.name" label="候选人" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="interviewer.name" label="面试官" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="index" label="面试轮次" min-width="8%" align="center">
            <template slot-scope="scope">
              <span>{{ (scope.row.index === 1) ? '第一轮' : '第二轮' }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="timeInterval.startTime"
            label="面试时间"
            min-width="10%"
            align="center"
          ></el-table-column>

          <el-table-column prop="score" label="面试结果" min-width="10%" align="center">
            <template slot-scope="scope">
              <span :class="getRateClass(scope.row.score)">{{scope.row.score}}</span>
              <el-link type="primary" @click="showRemark(scope.$index)">面试官评价</el-link>
            </template>
          </el-table-column>
          <el-table-column min-width="10%" align="center">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="decide(scope.$index)">决定面试结果</el-button>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          layout="total, prev, pager, next, jumper"
          :total="total_undecided"
          :page-size="page_size"
        ></el-pagination>
      </el-tab-pane>

      <el-tab-pane label="已处理面试结果">
        <div class="tag" v-if="noDecidedData">
          <i class="el-icon-warning-outline"></i>
          目前暂无已处理面试结果
        </div>
        <el-table :data="decidedData" :height="624" v-if="!noDecidedData">
          <el-table-column prop="interviewee.name" label="候选人" min-width="20%" align="center"></el-table-column>
          <el-table-column prop="interviewer.name" label="面试官" min-width="20%" align="center"></el-table-column>
          <el-table-column prop="index" label="面试轮次" min-width="20%" align="center">
            <template slot-scope="scope">
              <span>{{ (scope.row.index === 1) ? '第一轮' : '第二轮' }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="timeInterval.startTime"
            label="面试时间"
            min-width="20%"
            align="center"
          ></el-table-column>
          <el-table-column prop="result" label="结果" min-width="20%" align="center"></el-table-column>
        </el-table>

        <el-pagination
          layout="total, prev, pager, next, jumper"
          :total="total_decided"
          :page-size="page_size"
        ></el-pagination>
      </el-tab-pane>

      <el-tab-pane label="面试录像">
        <div class="tag" v-if="noRecordData">
          <i class="el-icon-warning-outline"></i>
          目前暂无面试录像
        </div>
        <el-table :data="recordData" :height="636" v-if="!noRecordData">
          <el-table-column prop="interviewee.name" label="候选人" min-width="20%" align="center"></el-table-column>
          <el-table-column prop="interviewer.name" label="面试官" min-width="20%" align="center"></el-table-column>
          <el-table-column prop="index" label="面试轮次" min-width="20%" align="center">
            <template slot-scope="scope">
              <span>{{ (scope.row.index === 1) ? '第一轮' : '第二轮' }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="timeInterval.startTime"
            label="面试时间"
            min-width="20%"
            align="center"
          ></el-table-column>
          <el-table-column min-width="10%" align="center">
            <template slot-scope="scope">
              <a :href="scope.row.link" target="_blank">
                <i class="el-icon-video-play" style="cursor:pointer"></i>
              </a>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          layout="total, prev, pager, next, jumper"
          :total="total_record"
          :page-size="page_size"
        ></el-pagination>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import NavMenu from './NavMenu'
export default {
  name: 'FinishedInterview',
  components: {
    NavMenu
  },
  methods: {
    getRateClass (score) {
      return 'rate' + score
    },
    showRemark (index) {
      this.dialogVisible = true
      this.dialogText = this.undecidedData[index].remark
    },
    decide (index) {
      this.decideVisible = true
      this.currentIndex = index
    },
    handleConfirm () {
      this.loading = true
      const self = this
      const path = this.$BASE_URL + '/admin/record/updateResult'
      this.$axios.post(path, null,
        {
          params: {
            intervieweeId: self.undecidedData[self.currentIndex].interviewee.id,
            interviewerId: self.undecidedData[self.currentIndex].interviewer.id,
            index: self.undecidedData[self.currentIndex].index,
            result: self.decideResult
          },
          headers: {
            token: sessionStorage.getItem('token')
          }
        })
        .then(function (response) {
          if (response.data.status === 'success') {
            self.loading = false
            self.decideVisible = false
            self.$message({
              message: '操作成功',
              type: 'success',
              showClose: true,
              duration: 3000
            })
            self.getUndecidedData()
          } else {
            self.$message({
              dangerouslyUseHTMLString: true,
              message: '操作失败<br>' + response.data.msg,
              type: 'error',
              duration: '3000',
              showClose: true
            })
          }
        })
        .catch(function (error) {
          self.$message({
            dangerouslyUseHTMLString: true,
            message: '操作失败<br>' + error,
            type: 'error',
            duration: '3000',
            showClose: true
          })
        })
    },
    getUndecidedData () {
      const self = this
      const path = this.$BASE_URL + '/admin/record/undecided'
      this.$axios.get(path,
        {
          params: {
            page: self.undecided_current_page,
            limit: self.page_size
          },
          headers: {
            token: sessionStorage.getItem('token')
          }
        })
        .then(function (response) {
          if (response.data.status === 'success') {
            self.undecidedData = response.data.data
            self.total_undecided = response.data.total
          } else {
            self.$message({
              dangerouslyUseHTMLString: true,
              message: '获取待处理面试信息失败<br>' + response.data.msg,
              type: 'error',
              duration: '3000',
              showClose: true
            })
          }
        })
        .catch(function (error) {
          self.$message({
            dangerouslyUseHTMLString: true,
            message: '获取待处理面试信息失败<br>' + error,
            type: 'error',
            duration: '3000',
            showClose: true
          })
        })
    },
    getDecidedData () {
      const self = this
      const path = this.$BASE_URL + '/admin/record/decided'
      this.$axios.get(path,
        {
          params: {
            page: self.decided_current_page,
            limit: self.page_size
          },
          headers: {
            token: sessionStorage.getItem('token')
          }
        })
        .then(function (response) {
          if (response.data.status === 'success') {
            self.decidedData = response.data.data
            self.total_decided = response.data.total
          } else {
            self.$message({
              dangerouslyUseHTMLString: true,
              message: '获取已处理面试信息失败<br>' + response.data.msg,
              type: 'error',
              duration: '3000',
              showClose: true
            })
          }
        })
        .catch(function (error) {
          self.$message({
            dangerouslyUseHTMLString: true,
            message: '获取已处理面试信息失败<br>' + error,
            type: 'error',
            duration: '3000',
            showClose: true
          })
        })
    },
    getRecordData () {
      const self = this
      const path = this.$BASE_URL + '/admin/record/list'
      this.$axios.get(path,
        {
          params: {
            page: self.record_current_page,
            limit: self.page_size
          },
          headers: {
            token: sessionStorage.getItem('token')
          }
        })
        .then(function (response) {
          if (response.data.status === 'success') {
            self.recordData = response.data.data
            self.total_record = response.data.total
          } else {
            self.$message({
              dangerouslyUseHTMLString: true,
              message: '获取面试录像失败<br>' + response.data.msg,
              type: 'error',
              duration: '3000',
              showClose: true
            })
          }
        })
        .catch(function (error) {
          self.$message({
            dangerouslyUseHTMLString: true,
            message: '获取面试录像失败<br>' + error,
            type: 'error',
            duration: '3000',
            showClose: true
          })
        })
    },
    handleClick (tab, event) {
      if (tab.label === '待处理面试结果') {
        this.undecided_current_page = 1
        this.getUndecidedData()
      } else if (tab.label === '已处理面试结果') {
        this.decided_current_page = 1
        this.getDecidedData()
      } else if (tab.label === '面试录像') {
        this.record_current_page = 1
        this.getRecordData()
      }
    }
  },
  data () {
    return {
      loading: false,
      page_size: 12,
      undecided_current_page: 1,
      decided_current_page: 1,
      record_current_page: 1,
      total_undecided: 0,
      total_decided: 0,
      total_record: 0,
      undecidedData: [],
      recordData: [],
      decidedData: [],
      dialogVisible: false,
      dialogText: '',
      currentIndex: 0,
      decideResult: '',
      decideVisible: false
    }
  },
  computed: {
    noUndecidedData: function () {
      return this.undecidedData.length === 0
    },
    noRecordData: function () {
      return this.recordData.length === 0
    },
    noDecidedData: function () {
      return this.decidedData.length === 0
    }
  },
  mounted () {
    document.title = 'Finished Interview'
    this.getUndecidedData()
  }
}
</script>

